.app-footer {
    display: flex;
    align-items: center;
    justify-content: center;
    bottom: 2px;
    right: 2px;
    position: fixed;
    .contact-logo {
        border-radius: 50%;
    }

    @include for-mobile-layout {
        .contacts-btn {
            font-size: 10px;
            height: 50px;
            width: 50px;
            border-radius: 50%;
            background-color: #ffdea3;
        }
    }
}
