.contact-details-page {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    img {
        padding: 10px;
        width: rem(500px);
    }
    .action-buttons {
        width: 100%;
        display: flex;
        justify-content: space-between;
    }
    .contact-details {
        background-color: rgba($color: white, $alpha: 0.7);
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }

    @include for-mobile-layout {
        img {
            width: rem(300px);
        }
    }
}
